@import url(https://fonts.googleapis.com/css?family=Fjalla+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:url("https://i.pinimg.com/originals/12/42/23/12422325859503a9f72d3ed7fb798710.png") center center no-repeat;background-attachment:fixed;background-size:cover;width:100vw;height:100vh;display:grid;justify-items:center;margin:0;padding:0}#root{margin-top:290px}.deepfreeze-root{background:#444444}.wallet-info{align-items:center;display:flex;position:fixed;right:10px;top:10px}.wallet-info .connect-wallet{margin-left:10px}.form{background:#f8f4e5;border:2px solid #000;box-shadow:15px 15px 1px #2A293E,15px 15px 1px 2px #000;justify-content:center;padding:50px 0;max-width:550px;box-sizing:border-box;width:100vw;display:flex;position:relative}.form tabs{position:absolute;top:-43px;left:-2px;width:100%;display:flex;justify-content:space-between;overflow:hidden}.form tabs tab{background-color:#2A293E;border:2px solid black;border-bottom:none;color:#fff;cursor:pointer;padding:10px 20px;position:relative;top:5px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.form tabs tab:not(.selected):hover{color:#95a4ff;-webkit-transform:translateY(-5px);transform:translateY(-5px);transition:-webkit-transform 0.25s ease-in-out;transition:transform 0.25s ease-in-out;transition:transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out}.form tabs tab.selected{cursor:default;top:0px}.FREEZER_view{width:100%;max-width:400px}.FREEZER_view .depositButton{background:#046}.FREEZER_view .depositButton:hover{background:#057;color:white}freezer{display:flex;justify-content:space-between;margin:50px 20px;border:2px solid #2A293E;padding:10px;align-items:end;position:relative}freezer h2{padding-bottom:1rem;margin:0;font-size:38px}freezer .depositAmount{margin-bottom:18px;min-width:0;min-width:initial;width:90px}freezer::after{content:' ';position:absolute;top:126px;left:0;background-image:url(/static/media/icicles.85622233.png);height:60px;width:100%;background-size:contain;-webkit-filter:saturate(0);filter:saturate(0)}.logo{position:fixed;left:0px;max-width:100vw;top:0px;width:400px}.logo img{width:100%}.light-section{position:relative}input,textarea{display:block;width:100%;font-size:14pt;line-height:28pt;font-family:"Roboto";margin-bottom:28pt;border:none;border-bottom:2px solid #000;background:#f8f4e5;min-width:300px;padding-left:5px;outline:none;color:#2A293E;box-shadow:0px 0px 0px 0px #2A293E;transition:box-shadow 0.1s ease-in}input:disabled,textarea:disabled{border-color:#444444}input:hover:disabled,textarea:hover:disabled{cursor:not-allowed}input:focus{box-shadow:0px 3px 0px 0px #2A293E}button,.button{cursor:pointer;color:#fff;display:block;margin:0 auto;line-height:28pt;padding:0 20px;background:#2A293E;letter-spacing:2px;transition:0.2s all ease-in-out;outline:none;border:1px solid #000;box-shadow:3px 3px 1px #95a4ff,3px 3px 1px 1px #000}button:disabled,.button:disabled{background-color:#888888}button:hover,.button:hover{background:#15151f;color:#95a4ff;border:1px solid #15151f}button:hover:disabled,.button:hover:disabled{background-color:#888888;color:#CC3333;cursor:not-allowed}.howToHash{margin-top:-25px}.howToHash a{position:relative;top:-3px}button.submit{margin-top:50px}::selection{background:#ffc8ff}input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus{border-bottom:5px solid #95a4ff;-webkit-text-fill-color:#2A293E;-webkit-box-shadow:0 0 0px 1000px #f8f4e5 inset;-webkit-transition:background-color 5000s ease-in-out 0s;transition:background-color 5000s ease-in-out 0s}.input-with-icon{position:relative}.input-icon{left:-14px;position:absolute;top:26px}

